import {
  findMerchantUser,
  switchMerchantAccountState,
  addMerchantUser,
  updateMerchantUserPwd,
} from "../../../../common/api/merchant";
import * as constants from "./contants";
import { message } from "antd";

export const getMerchantUserInfo = (merchantId) => {
  return (dispatch) => {
    findMerchantUser(merchantId)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setAccountList(res.data.result));
        } else {
          message.warning(res.data.message || "获取商户账户信息失败！");
        }
      })
      .catch((err) => {
        message.error("获取商户账户信息异常！");
      });
  };
};

export const switchAccountState = (params) => {
  let merchantId = params.merchantId;
  return (dispatch) => {
    switchMerchantAccountState(params)
      .then((res) => {
        if (res.data.code === 200) {
          message.success("修改商户状态成功！");
          //刷新
          dispatch(getMerchantUserInfo(merchantId));
        } else {
          message.warning(res.data.message || "修改商户账户状态失败！");
        }
      })
      .catch((err) => {
        message.error("修改商户账户状态异常！");
      });
  };
};

export const setAccountList = (data) => ({
  type: constants.SET_ACCOUNT_LIST,
  playload: data,
});

export const addMerchantUserInfo = (data) => {
  addMerchantUser(data)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("创建用户成功！");
      } else {
        message.error(res.data.message || "创建用户失败！");
      }
    })
    .catch((err) => {
      message.error("创建用户异常！");
    });
};

export const updateMerchantPwd = (data) => {
  updateMerchantUserPwd(data)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("重置密码成功！");
      } else {
        message.error(res.data.message || "重置密码失败！");
      }
    })
    .catch((err) => {
      message.error("重置密码异常！");
    });
};
