import axios from "../request";

// 获取商户列表
export function getMerchantList(pageNum, pageSize, ...args) {
  let url = "/biz-admin/merchant/findByPage?";
  url += "pageNum=" + pageNum + "&";
  url += "pageSize=" + pageSize;
  let arr = Object.keys(args);
  arr.forEach((key, index) => {
    if (index === arr.length - 1) {
      url += +"&" + key + "=" + args[key];
    } else {
      url += +"&" + key + "=" + args[key] + "&";
    }
  });
  return axios.get(url);
}

// 获取省列表
export function getProviceList() {
  return axios.get("/biz-admin/base/region/0");
}

// 获取市列表
export function getCityList() {
  return axios.get("/biz-admin/base/region/1");
}

// 获取市列表
export function getAreaList() {
  return axios.get("/biz-admin/base/region/2");
}

// 商户平台菜单列表
export function getMerchantMenuList() {
  return axios.get("/biz-admin/merchant/menu/findParentMenu");
}

// 获取某个商户开通的菜单列表
export function getMerchantMenu(merchantId) {
  const url = "/biz-admin/merchant/menu/findAuthority/" + merchantId;
  return axios.get(url);
}

// 获取某个商户下的账户信息
export function findMerchantUser(merchantId) {
  const url = "/biz-admin/merchant/user//findMerchantUser/" + merchantId;
  return axios.get(url);
}

// 商户用户的权限开关
export function switchMerchantAccountState(params) {
  return axios.post("/biz-admin/merchant/switch", params);
}

// 添加商户用户
export function addMerchantUser(params) {
  return axios.post("/biz-admin/merchant/user/addAdminUser", params);
}

// 修改商户用户密码
export function updateMerchantUserPwd(params) {
  return axios.post("/biz-admin/merchant/user/resetPwd", params);
}

// 根据商户id查询商户信息
export function getMerchantInfo(merchantId) {
  return axios.get("/biz-admin/merchant/" + merchantId);
}

// 切换商户状态
export function switchMerchantState(params) {
  return axios.post("/biz-admin/merchant/switch", params);
}

// 添加商户
export function addMerchantInfo(params) {
  return axios.post("/biz-admin/merchant/add", params);
}

// 添加商户权限
export function addMerchantMenu(params) {
  return axios.post("/biz-admin/merchant/menu/addAuthority", params);
}
