import * as constants from "./constants";
import {
  roleListByPage,
  addRole,
  updateRole,
  switchRoleState,
  getMenuTree,
} from "../../../../common/api/role";
import { message } from "antd";

export const getRoleTableData = (params) => {
  return (dispatch) => {
    roleListByPage(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setRoleList(res.data.result.list));
        } else {
          message.warning(res.data.message || "获取角色信息失败！");
        }
      })
      .catch((err) => {
        console.error(err);
        message.warning("获取角色信息异常！");
      });
  };
};

export const setRoleList = (data) => ({
  type: constants.SET_ROLE_LIST,
  playload: data,
});

export const addRoleInfo = (params) => {
  return (dispatch) => {
    addRole(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(getRoleTableData());
          message.success("添加角色成功！");
        } else {
          message.warning(res.data.message || "添加角色失败！");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("添加角色失败！");
      });
  };
};

export const updateRoleInfo = (params) => {
  return (dispatch) => {
    updateRole(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(getRoleTableData());
          message.success("修改角色成功！");
        } else {
          message.warning(res.data.message || "修改角色失败！");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("修改角色失败！");
      });
  };
};

export const updateRoleState = (params) => {
  return (dispatch) => {
    switchRoleState(params)
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(getRoleTableData());
          message.success("修改状态成功！");
        } else {
          message.warning(res.data.message || "修改状态失败！");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("修改状态失败！");
      });
  };
};

export const getMenuTreeList = () => {
  return (dispatch) => {
    getMenuTree()
      .then((res) => {
        if (res.data.code === 200) {
          dispatch(setMenuTree(res.data.result || []));
        } else {
          message.error(res.data.message || "初始化权限列表异常!");
        }
      })
      .catch((err) => {
        console.error(err);
        message.error("初始化权限列表异常!");
      });
  };
};

export const setMenuTree = (data) => ({
  type: constants.SET_MENU_TREE,
  playload: data,
});
