import axios from "../request";

// 角色列表分页
export function roleListByPage(params) {
  if (!params) {
    return axios.get("/biz-admin/role/findByPage");
  }
  let baseUrl = "/biz-admin/role/findByPage?";
  let keyArr = Object.keys(params);
  keyArr.forEach((key, index) => {
    if (index !== keyArr.length - 1) {
      baseUrl += key + "=" + params[key] + "&";
    } else {
      baseUrl += key + "=" + params[key];
    }
  });
  return axios.get(baseUrl);
}

// 获取角色树
export function getMenuTree() {
  return axios.get("/biz-admin/menu/getMenuTree");
}

// 查看某个角色的菜单权限

// 添加角色
export function addRole(params) {
  return axios.post("/biz-admin/role/add", params);
}

// 修改角色
export function updateRole(params) {
  return axios.post("/biz-admin/role/update", params);
}

// 修改角色状态
export function switchRoleState(params) {
  return axios.post("/biz-admin/role/switch", params);
}
